/* Navigation Hover Effects */

nav > a:hover{
  transition: all .4s;
  -webkit-animation: nav .25s cubic-bezier(.25,.46,.45,.94) both infinite;
  animation: nav .25s cubic-bezier(.25,.46,.45,.94) both infinite;
}

@-webkit-keyframes nav {
  to {
    box-shadow: 5px 0 0 #f0f, -5px 0 0 #0ff
  }
}

@keyframes nav {
  0% {
    box-shadow: 3px 0 0 #f0f, -2px 0 0 #0ff
  }

  33% {
    box-shadow: 4px 0 0 #f0f, -4px 0 0 #0ff
  }

  66% {
    box-shadow: -3px 0 0 #f0f, 3px 0 0 #0ff
  }

  to {
    box-shadow: 5px 0 0 #f0f, -5px 0 0 #0ff
  }
}

/* Box Hover Effects */

.hover-remove-shadow:hover {
  box-shadow: 0 0 0 0 #000;
  margin: 4px -4px -4px 4px;
}

.box-transition-100 {
  transition: all 0.7s ease 0.1s, margin 0.4s ease 0s, box-shadow 0.4s ease 0s !important;
}

.box-transition-150 {
  transition: all 0.7s ease 0.15s, margin 0.4s ease 0s, box-shadow 0.4s ease 0s !important;
}

.box-transition-200 {
  transition: all 0.7s ease 0.2s, margin 0.4s ease 0s, box-shadow 0.4s ease 0s !important;
}

/* Launch Button Styles */

.launch-button-outer{
  width: 248px;
  height: 48px;
  background-color: black;
  margin-top: 1px;
  margin-left: 9px;
  -webkit-transform: translate(-50%, 50%);
  transform: translate(-50%, 50%);
}

.launch-button-inner {
  display: inline-block;
  padding: 12px 42px;
  text-decoration: none;
  transition: all .2s;
  background-color: #FFB300;
  -webkit-transform: translate(-50%, 50%);
  transform: translate(-50%, 50%);
}

.launch-button-inner::before {
  position: absolute;
  content: '';
  transition: all .2s;
  bottom: -8px;
  height: 8px;
  width: 100%;
  left: 4.5px;
  -webkit-transform: skewX(45deg);
  transform: skewX(45deg);
  background-color: #FB9608;
}

.launch-button-inner::after {
  position: absolute;
  content: '';
  transition: all .2s;
  right: -8px;
  height: 102%;
  width: 8px;
  bottom: -4.5px;
  -webkit-transform: skewY(45deg);
  transform: skewY(45deg);
  background-color: #FB9608;
}

.launch-button-inner:active {
  margin-left: 5px;
  margin-top: 5px;
}

.launch-button-inner:active::before {
  bottom: -2.5px;
  height: 2.5px;
  left: 1.5px;
}

.launch-button-inner:active::after {
  right: -2.5px;
  width: 2.5px;
  bottom: -1.5px;
}

/* Roadmap Grid Layout */

.roadmap-wrapper {
  grid-template-columns: 1fr 5px 1fr;
  grid-template-rows: repeat(14, 1fr);
}

.roadmap-q2{
  grid-row: 2 / 8;
  grid-column: 1 / 2;
}

.roadmap-q3{
  grid-row: 4 / 12;
  grid-column: 3 / 4;
}

.roadmap-q4{
  grid-row: 9 / 14;
  grid-column: 1 / 2;
}

.roadmap-line{
  grid-row: 1 / 15;
  grid-column: 2 / 3;
}

@media only screen and (max-width: 1023px) {
  .roadmap-wrapper {
    grid-template-columns: auto;
    grid-template-rows: auto;
    grid-row-gap: 60px;
  }

  .roadmap-q2{
    grid-row: auto;
    grid-column: auto;
    -webkit-order: 1;
    order: 1;
  }

  .roadmap-q3{
    grid-row: auto;
    grid-column: auto;
    -webkit-order: 2;
    order: 2;
  }

  .roadmap-q4{
    grid-row: auto;
    grid-column: auto;
    -webkit-order: 3;
    order: 3;
  }

  .roadmap-line{
    grid-row: auto;
    grid-column: auto;
  }
}

